footer {
  position: fixed;
  z-index: -10;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 80px;
  width: 100%;
  padding: 20px;
  background-color: #5C7CA8;
  /*background-blend-mode: multiply;
  background-image: url('../../assets/hero-image.jpg');
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;*/
  color: #fff;
  text-shadow: 0px 1px 1px #2b3b52;
  font-size: 12px;
  font-weight: 600;
  font-family: "Open Sans";
}

footer .made-by {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

footer a {
  color: #fff;
  border-bottom: 1px solid transparent;
}

footer a:hover {
  border-color: #fff;
}

.heart-icon {
  height: 11px;
  margin: 0 5px;
  -webkit-filter: drop-shadow(0px 1px 1px #2b3b52);
          filter: drop-shadow(0px 1px 1px #2b3b52);
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
}

@-webkit-keyframes pulse {
  15% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  85% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  15% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
  85% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@media (max-width: 800px) {
  footer {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}



